p {
  padding: 0;
}

.categorys {
  width: 307px;
  height: 228px;
  gap: 20px;
  margin: 0 auto;
  display: flex;
  overflow-y: scroll;
}

.categorys::-webkit-scrollbar {
  width: 8px;
  height: 114px;
}

.categorys::-webkit-scrollbar-thumb {
  width: 8px;
  height: 114px;
  background: #e0e0e0;
  border-radius: 12px;
}

.category-list {
  flex-direction: column;
  align-items: flex-start;
  gap: 24px;
  list-style: none;
  display: flex;
}

.category-link {
  color: rgba(17, 17, 17, .6);
  letter-spacing: -.32px;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  text-decoration: none;
}

.title-category {
  margin-bottom: 40px;
}

.title-secondary {
  color: #4f2ee8;
}

.books-category {
  margin: 0 auto;
}

.book-carts {
  width: 335px;
  height: 535px;
  flex-direction: column;
  align-items: flex-start;
  gap: 14px;
  margin-bottom: 40px;
  display: flex;
}

.book-img {
  width: 335px;
  height: 485px;
  border-radius: 8px;
}

.go-top {
  cursor: pointer;
  width: 54px;
  height: 54px;
  background-color: var(--main-accent-color);
  z-index: 1000;
  border: 5px solid rgba(27, 179, 234, .918);
  border-radius: 50%;
  padding: 0;
  display: none;
  position: fixed;
  bottom: 145px;
  right: 25px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

.go-top-icon {
  width: 24px;
  height: 24px;
  fill: var(--main-bg-color);
}

.go-top-show {
  justify-content: center;
  align-items: center;
  display: flex;
}



